
document.addEventListener('DOMContentLoaded', function(){

    window.map = {
        list: document.querySelectorAll('.jsMap'), 
    }
    // console.log(window.map);
    
    for (let index = 0; index < window.map.list.length; index++) {
        const item = window.map.list[index];
        let coordinates = JSON.parse(item.getAttribute('data-map-coordinates'));
        // const id = item.getAttribute('id')
        // console.log(coordinates);

        function init(){
            var thisMap = new ymaps.Map(item, {
                // Координаты центра карты.
                // Порядок по умолчанию: «широта, долгота».
                // Чтобы не определять координаты центра карты вручную,
                // воспользуйтесь инструментом Определение координат.
                center: coordinates[0],
                // Уровень масштабирования. Допустимые значения:
                // от 0 (весь мир) до 19.
                zoom: 7
            });

        }
        ymaps.ready(init)

        // thisMap;

        // window.modal[id] = itemModal;
    }
    
    // window.map.list.forEach(function(e) {

    // });

    // console.log(window.map);

    // window.map.callback.show()
    // window.map.chanels.show()
    // window.map.success.show()
    // window.map.id.hide()
})
